import React, { useState } from "react"
import {Collapse, Fade} from 'react-bootstrap'
import "./card.css"
import GitHubIcon from '@material-ui/icons/GitHub';
import VisibilityIcon from '@material-ui/icons/Visibility';
function Class(props){
    const [open, setOpen] = useState(false)
    return(

            <div className="project">
                <div className="header">
                    <button onClick={() => setOpen(!open)} className='header-text'>
                        {props.data.name}
                    </button>
                </div>

                <Collapse in={open}>
                    <div id={`project${props.index}-details`}>
                        <div className="card-body">
                            {props.data.details}
                            <div className="project-links">
                                {props.data.git && <a href={props.data.git} target="_blank" rel="noreferrer" className="link"><GitHubIcon/></a>}
                                {props.data.link && <a href={props.data.link} target="_blank" rel="noreferrer" className='link' title="View Site"><VisibilityIcon/></a>}
                               
                            </div>
                            <div className="project-preview">
                                {props.data.image && <img className="previewImage" src={'img/'+props.data.image} alt={props.data.name + " preview image"}/>}
                                {props.data.youtube && 
                                    <iframe className="previewImage" src="https://www.youtube.com/embed/l3kC2-ahhZU" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                                }
                            </div>
                        </div>

                    </div>
                </Collapse>

            </div>
    )
}

export default Class
