import {useState, useEffect} from 'react'
import profile from '../img/Ma.Bradley.jpg';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import './intro.css'
import Typist from 'react-typist';
import 'react-typist/dist/Typist.css'
function Intro () {
    const data_rotate = ["college", "Javascript", "coding in Python", "React", "a pandemic (Stay safe)"]
    const [index, setIndex] = useState(0)

    useEffect( () => {
        const interval = setInterval( () => {
            setIndex(index => (index + 1) % data_rotate.length);
        }, 5000);
        return () => clearInterval(interval);
    })
    return (
        <header id="about-me" className="App-header">
          <div className="">
            <div className="">
              <div className="profile-frame">
                <img src={profile} alt="Headshot" className="profile-pic"/>
              </div>
              <h1 className="myname">Bradley Ma</h1>
                <div className="links">
                    <div className="row">
                        <a href="https://github.com/bradleyyma" target="_blank" className="icon">
                            <GitHubIcon fontSize="large"/>
                        </a>
                        <a href="https://www.linkedin.com/in/bradleyyma/" target="_blank" className="icon"> 
                            <LinkedInIcon fontSize="large"/>
                        </a>
                        <a title="Resume" href="https://docs.google.com/document/d/1QOEAiRLSEbMEwohLhvCo5oO6pB2tsH_aj5HqOF6SlG4/edit?usp=sharing" target="_blank" className="icon">
                            <AccountCircleIcon fontSize="large" />
                        </a>
                    </div>
              </div>
            </div>
            <div className="aboutme">
              <p className="">Hi and welcome! I am currently a computer science student at the University of Colorado Boulder. I'm learning to code, but I can learn more! This portfolio is here to show my progress and what I've created so far.  </p>
              <p>
                  Currently messing with:
                  <Typist key={index} className="carousel">
                       {data_rotate[index]}
                       <Typist.Backspace count={data_rotate[index].length} delay = {1000} />
                  </Typist>
              </p>
            </div>
          </div>
          <a href="#projects" className="arrow" title="Scroll To Projects">
            <svg xmlns="http://www.w3.org/2000/svg" height="4vmin" fill="currentColor" className="bi bi-arrow-bar-down" viewBox="0 0 16 16">
              <path fill-rule="evenodd" d="M1 3.5a.5.5 0 0 1 .5-.5h13a.5.5 0 0 1 0 1h-13a.5.5 0 0 1-.5-.5zM8 6a.5.5 0 0 1 .5.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 0 1 .708-.708L7.5 12.293V6.5A.5.5 0 0 1 8 6z"/>
            </svg>
          </a>
        </header>
    )

}

export default Intro;
